<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`Loan${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import LoanListLayout from '@/views/loan/loan-root/loan-browse/LoanListLayout'
import LoanCardLayout from '@/views/loan/loan-root/loan-browse/LoanCardLayout'
import LoanPivotLayout from '@/views/loan/loan-root/loan-browse/LoanPivotLayout'
import LoanTableMixin from '@/mixins/loan/LoanTableMixin'

export default {
  name: 'loan-browse',
  mixins: [LoanTableMixin],
  components: {
    LoanListLayout,
    LoanCardLayout,
    LoanPivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    this.loadLoanTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
