import LoanService from '@/services/loan/LoanService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let LoanTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  data: function () {
    return {
      loanTableData: [],
      loanTableContainer: 'loading-container',
      loanTableBusy: false,
      loanTableTotalRows: 0,
      loanTableCurrentPage: 1,
      loanTablePerPage: 10,
      columnDefs: [{
        headerName: this.$t('LOAN_NUMBER'),
        field: 'loanNumber',
        format: 'Number'
      }, {
        headerName: this.$t('LOAN_TYPE'),
        field: 'typeName'
      }, {
        headerName: this.$t('START_DATE'),
        field: 'startDate',
        format: 'Date'
      }, {
        headerName: this.$t('CURRENCY'),
        field: 'currencyAbbreviation'
      }, {
        headerName: this.$t('PRINCIPAL_DISBURSED'),
        field: 'loanAmount',
        format: 'Decimal'
      }, {
        headerName: this.$t('OUTSTANDING_AMOUNT'),
        field: 'outstandingAmount',
        format: 'Decimal'
      }]
    }
  },
  computed: {
    loanTableNoDataText: function () {
      return this.loanTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    loanTableTotalPages: function () {
      return Math.ceil(this.loanTableTotalRows / this.loanTablePerPage)
    },
    loanTablePagingInfo: function () {
      return this.pagingInfo(this.loanTableCurrentPage, this.loanTablePerPage, this.loanTableTotalRows)
    }
  },
  methods: {
    loadLoanTableData (loadingContainerId) {
      let self = this
      this.loanTableBusy = true
      this.loanTableContainer = loadingContainerId || this.loanTableContainer
      this.startLoading(this.loanTableContainer)
      LoanService.loans({}).then(function (response) {
        self.stopLoading(self.loanTableContainer)
        self.loanTableBusy = false
        self.loanTablePerPage = response.data.totalRows
        self.loanTableTotalRows = response.data.totalRows
        self.loanTableData = response.data.loanList || []
      }).catch(function (error) {
        self.stopLoading(self.loanTableContainer)
        self.loanTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshLoanTable () {
      this.loadLoanTableData()
    }
  },
  watch: {
    loanTableCurrentPage: function () {
      this.refreshLoanTable()
    }
  }
}

export default LoanTableMixin
