<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center flex-grow justify-between mb-base">
      <div class="flex flex-wrap-reverse items-center">
        <vs-button v-if="parent.loanTableData.length > 0" class="p-3 mr-4" @click="exportXls" type="filled" icon="fa-file-excel" icon-pack="fal">{{ $t('EXPORT_XLS') }}</vs-button>
        <vs-button v-if="parent.loanTableData.length > 0" class="p-3 mr-4" @click="exportCsv" type="filled" icon="fa-file-csv" icon-pack="fal">{{ $t('EXPORT_CSV') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="parent.refreshLoanTable" type="filled" icon="fa-sync" icon-pack="far">{{ $t('REFRESH') }}</vs-button>
      </div>
    </div>

    <ag-grid-vue class="ag-theme-balham ag-pivot-layout"
                 :gridOptions="gridOptions"
                 :rowData="parent.loanTableData"
                 :sideBar="sideBar"
                 :statusBar="statusBar">
    </ag-grid-vue>
  </nv-card>
</template>

<script>
import PivotLayoutMixin from '@/mixins/commons/PivotLayoutMixin'
import { AgGridVue } from 'ag-grid-vue'

export default {
  name: 'loan-pivot-layout',
  mixins: [PivotLayoutMixin],
  components: {
    AgGridVue
  },
  beforeMount () {
    this.gridOptions.columnDefs = [{
      headerName: this.$t('LOAN_NUMBER'),
      field: 'loanNumber'
    }, {
      headerName: this.$t('LOAN_TYPE'),
      field: 'typeName',
      enableRowGroup: true
    }, {
      headerName: this.$t('START_DATE'),
      field: 'startDate',
      enableRowGroup: true,
      filter: 'agDateColumnFilter',
      valueFormatter: this.formatDate
    }, {
      headerName: this.$t('CURRENCY'),
      field: 'currencyAbbreviation',
      enableRowGroup: true
    }, {
      headerName: this.$t('PRINCIPAL_DISBURSED'),
      field: 'loanAmount',
      type: 'numericColumn',
      filter: 'agNumberColumnFilter',
      valueFormatter: this.formatNumber,
      enableValue: true
    }, {
      headerName: this.$t('BALANCE_OUTSTANDING'),
      field: 'outstandingAmount',
      type: 'numericColumn',
      filter: 'agNumberColumnFilter',
      valueFormatter: this.formatNumber,
      enableValue: true
    }]
  }
}
</script>
